<script>
  import { getAuth, signOut } from 'firebase/auth'
  import axios from 'axios'

  export default {
    data() {
      return {
        API_SUCCESS : 200,
        API_FAILED_LOGIN : 99,
        API_FAILED_AUTH : 100,
        API_FAILED_PARAM : 300,
        API_FAILED_NODATA : 400,
        API_FAILED_DUPLICATE : 401,
        API_FAILED_MISMATCH : 402,
        API_FAILED_PRIVATE : 500,
        API_FAILED_FILE : 600,
        API_FAILED_EXEPTION : 900,

        LENGTH_MAIL : 50,
        LENGTH_PASSWORD : 50,

        // ROOT_PATH : "./",
        // DOMAIN_PATH : "localhost",

        IMG_PATH : "./app/img/",
        ICON_PATH : "./storage/icon/",
        // SERVER_PATH : "http://localhost:8001/api/",
        SERVER_PATH : "https://iphone-taro.sakura.ne.jp/yumedrop/api/",
        // CSRF_URL : "https://iphone-taro.sakura.ne.jp/yumedrop/sanctum/csrf-cookie",
        // CSRF_URL : "http://localhost:8001/sanctum/csrf-cookie",

        reloadValue : "",

        isLogin: false,
        token : "",

        PAGE : "",

        navClass : '',
        userData : {
          userId : "",
          userName : "",
          profile : ""
        },

        tabImgSrcs : [
          this.IMG_PATH + 'tab/icon_tab_search_off.png',
          this.IMG_PATH + 'tab/icon_tab_info_off.png',
          this.IMG_PATH + 'tab/icon_tab_setting_off.png',
          this.IMG_PATH + 'tab/icon_tab_edit_off.png',
          this.IMG_PATH + 'tab/icon_tab_postlist_off.png',
          this.IMG_PATH + 'tab/icon_tab_follow_off.png',
          this.IMG_PATH + 'tab/icon_tab_bookmark_off.png',
        ],

        tabClasses : [
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
        ],

        isPopup: false,
        popupKbn: "",
        popupTitle: "",
        popupBody: "",
      };
    },
    created() {
      console.log(this.IMG_PATH);
      // getAuth().onAuthStateChanged(user => {
      //   console.log(this.$cookies);
      //     console.log("ログイン状態取得!");

      //     if (user != null) {
      //       console.log(user);

      //       console.log("ログイン");
      //       // this.isLogin = true;
      //       // this.$router.push('/mainmenu');
      //     } else {
      //       console.log("未ログイン");
      //       this.isLogin = false;

      //       this.userData = {
      //         userId : "",
