      //       this.$router.push('/');
      //     }
      // });
    },
    methods : {
      pushLogin: function () {
        this.$router.push('/login');
      },
      checkServerResponse : function (response) {
        //サーバー返答時の共通処理
        console.log(response.data);
        let resData = response.data;
        if (resData["status"] == this.API_FAILED_AUTH) {
          //未認証エラー
          this.isLogin = false;
          this.$router.push("/");
        } else if (resData["status"] != this.API_SUCCESS) {
          //エラー
          this.$router.push({path : "/error", query : {errorCode : resData["status"], message : resData["errMsg"]}});
          return false;
        }

        if (resData["baseInfo"] != null) {
          //ログイン時
          //基本情報を設定
          this.isLogin = true;
          let baseData = resData["baseInfo"];
          this.userData = {
            userId : baseData["user_id"],
            userName : baseData["user_name"],
            profile : baseData["profile"]
          }
        } else {
          //非ログイン
          this.isLogin = false;
          this.userData = {
            userId : "",
            userName : "",
            profile : ""
          }
        }
        return true;
      },
      pushHome : function () {
        if (this.isLogin) {
          this.$router.push("/mainmenu");
        } else {
          this.$router.push("/");
        }
      },
      pushBtn : function (name) {
        this.pushMenu();
        if (name == "search") {
          this.$router.push("/search");
        } else if (name == "edit") {
          this.$router.push("/edit/new");
        } else if (name == "postlist") {
          this.$router.push({ path:"/postlist", query : {series : "すべて", page : 1}});
        } else if (name == "bookmark") {
          this.$router.push({ path:"/bookmark", query : {series : "すべて", page : 1}});
        } else if (name == "follow") {
          this.$router.push({ path:"/follow", query : {page : 1}});
        } else if (name == "setting") {
          this.$router.push("/setting");
        }
      },
      pushMenu : function () {
        if (this.navClass == '') {
          this.navClass = 'panelactive';
        } else {
          this.navClass = '';
        }
      },
      Logout : function (flg) {
        console.log("Logout");
        if (flg) {
          this.pushMenu();
        }

        axios.get('/sanctum/csrf-cookie').then(response => {
          console.log(response);
          axios.get(this.$root.SERVER_PATH + 'logout')
          .then((response) => {
            let resData = response.data;
            console.log(resData["status"] );
          
            if (!this.$root.checkServerResponse(response)) {
              return;
            }
            
            signOut(getAuth());
            this.userData = {
              userId : "",
              userName : "",
              profile : "",
            };
            this.$router.push('/');
            this.isLogin = false;
          })
          .catch((e) => {
            alert(e);
          });
        });
      },
      getBaseInfo : function() {
        axios.get(this.$root.SERVER_PATH + 'getBaseInfo')
        .then((response) => {
          if (!this.checkServerResponse(response)) {
            return;
          }
        });
      },
      arrangeStr(value, splitStr) {
        if (value == "") {
          return value;
        }
        let retStr = "";
        let strArray = value.split(splitStr);
        for (let i = 0; i < strArray.length; i++) {
          if (strArray[i] != "") {
            retStr = retStr + strArray[i];

            if (i != strArray.length - 1) {
              retStr = retStr + splitStr;
            }
          }
        }
        return retStr;
      },
      setCurrentPage : function (page) {
        console.log("IN " + page);
        this.PAGE = page;

        //アクティブタブ
        this.tabImgSrcs = [
          this.IMG_PATH + 'tab/icon_tab_search_off.png',
          this.IMG_PATH + 'tab/icon_tab_info_off.png',
          this.IMG_PATH + 'tab/icon_tab_setting_off.png',
          this.IMG_PATH + 'tab/icon_tab_edit_off.png',
          this.IMG_PATH + 'tab/icon_tab_postlist_off.png',
          this.IMG_PATH + 'tab/icon_tab_follow_off.png',
          this.IMG_PATH + 'tab/icon_tab_bookmark_off.png',
        ];

        this.tabClasses = [
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
          "menuIcon",
        ];

        if (this.PAGE == "SEARCH") {
          this.tabImgSrcs[0] = this.IMG_PATH + 'tab/icon_tab_search_on.png';
          this.tabClasses[0] = "menuIcon currentTab";
        } else if (this.PAGE == "INFO") {
          this.tabImgSrcs[1] = this.IMG_PATH + 'tab/icon_tab_info_on.png';
          this.tabClasses[1] = "menuIcon currentTab";
        } else if (this.PAGE == "SETTING") {
          this.tabImgSrcs[2] = this.IMG_PATH + 'tab/icon_tab_setting_on.png';
          this.tabClasses[2] = "menuIcon currentTab";
        } else if (this.PAGE == "EDIT") {
          this.tabImgSrcs[3] = this.IMG_PATH + 'tab/icon_tab_edit_on.png';
          this.tabClasses[3] = "menuIcon currentTab";
        } else if (this.PAGE == "POSTLIST") {
          this.tabImgSrcs[4] = this.IMG_PATH + 'tab/icon_tab_postlist_on.png';
          this.tabClasses[4] = "menuIcon currentTab";
        } else if (this.PAGE == "FOLLOW") {
          this.tabImgSrcs[5] = this.IMG_PATH + 'tab/icon_tab_follow_on.png';
          this.tabClasses[5] = "menuIcon currentTab";
        } else if (this.PAGE == "BOOKMARK") {
          this.tabImgSrcs[6] = this.IMG_PATH + 'tab/icon_tab_bookmark_on.png';
          this.tabClasses[6] = "menuIcon currentTab";
        }
      },
      showPopup: function(kbn, title, body) {
        document.getElementById('body').className = "modalBg";
        this.popupKbn = kbn;
        this.popupTitle = title;
        this.popupBody = body;
        this.isPopup = true;
      },
      closePopup : function () {
        if (this.popupKbn == "TIPS") {
          document.getElementById('body').className = "";
          this.isPopup = false;
        }
      },
      pushTips: function(index) {
        if (index == 0) {
          this.showPopup("TIPS", "変換項目", "変換項目の説明です");
        } else if (index == 1) {
          this.showPopup("TIPS", "シリーズ", "シリーズの説明です");
        } else if (index == 1) {
          this.showPopup("TIPS", "シリーズ", "シリーズの説明です");
        } else if (index == 1) {
          this.showPopup("TIPS", "シリーズ", "シリーズの説明です");
        } else if (index == 1) {
          this.showPopup("TIPS", "シリーズ", "シリーズの説明です");
        }
      },
      retComSeriesList : function(dataList) {
        let seriesList = [];
        seriesList.push("すべて");

        for (let i = 0; i < dataList.length; i++) {
          var series = dataList[i].series;
          var flg = true;
          for (let j = 0; j < seriesList.length; j++) {
            if (seriesList[j] == series) {
              flg = false;
            }
          }
          if (flg) {
            seriesList.push(series);
          }
        }
        return seriesList;
      },
      retComFilterList : function(dataList, series) {
        let filterDataList = [];
        for (let i = 0; i < dataList.length; i++) {
          var data = dataList[i];
          //シリーズの絞り込み
          if (series == "すべて" || data.series == series) {
            filterDataList.push(data);
          }
        }
        return filterDataList;
      },
      retComPagingList : function (page, dataList) {
        let retArray = [];
        let max = (page - 1) * 10 + 10;
        if (dataList.length < (page - 1) * 10 + 10) {
          max = dataList.length;
        }

        for (let i = (page - 1) * 10; i < max; i++) {
          retArray.push(dataList[i]);
        }
        return retArray;
      },
    }
  }
</script>

