<template>
  <div class="login" style="text-align:center; width:270px; margin:auto">
    <p class="p18" style="margin-top:20px">IDとPASSでログイン</p>
    <div style="margin-top:20px; text-align:left">
      <span class="errMsg" v-if="errorMessageData.id">▼ {{errorMessageData.id}}<br></span>
      <input type="text" class="input-text" style="width:100%" v-model="id" maxlength="50"><br>
      <div style="height:10px" />
      <span class="errMsg" v-if="errorMessageData.password">▼ {{errorMessageData.password}}<br></span>
      <input type="password" class="input-text" style="width:100%" v-model="password" maxlength="50"><br>
    </div>
    <div style="height:20px" />
    <span class="errMsg" v-if="errorMessageData.login">▼ {{errorMessageData.login}}<br></span>
    <span class="btn btn-pink btn-icon btn-icon-key" style="width:100%;" v-on:click="pushIDLogin">IDとPASSでログイン</span>
    <div style="margin-top:10px">
      <span class="link" v-on:click="pushReissue">パスワードを忘れた方はこちら</span>
    </div>
    <p class="p14" style="margin-top:30px">初めての方はこちら</p>
    <span class="btn btn-yellow" style="width:100%" v-on:click="pushRegistration">IDとPASSで新規登録</span>

    <hr class="line" style="margin-top:20px">
    
    <p class="p14" style="margin-top:15px">Twitterアカウントで登録/ログイン</p>
    <span class="btn btn-pink btn-icon btn-icon-twitter" style="width:100%;" v-on:click="pushTwitterLogin">ログイン</span>
    <br>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
import { getAuth, setPersistence, TwitterAuthProvider, signInWithPopup, browserLocalPersistence } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'Login',
  components: {
  },
  data() {
    return {
      id : "",
      password : "",

      errorMessageData : {
        id : "",
        password : "",
        login : "",
      }
    };
  },
  computed : {
  },
  methods : {
    pushIDLogin : function () {
      console.log("IN");
      this.errorMessageData.id = "";
      this.errorMessageData.password = "";
      this.errorMessageData.login = "";

      //トリム
      this.id = this.id.trim();
      this.password = this.password.trim();
      
      //入力チェック
      if (this.id == "") {
        this.errorMessageData.id = "IDが未入力です";
      } else if (this.id.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.id = "IDは50文字以内で入力してください";
      }
      
      if (this.password == "") {
        this.errorMessageData.password = "PASSWORDが未入力です";
      } else if (this.password.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.password = "パスワードは50文字以内で入力してください";
      }

      if (this.errorMessageData.id != "" || this.errorMessageData.password != "") {
        return;
      }
      
      let params = new URLSearchParams();
      params.append('mailAddress', this.id);
      params.append('password', this.password);
      let method = "m_login";

      this.login(params, method);
    },
    pushTwitterLogin : function () {
      this.signinTwitter();
    },
    pushReissue : function () {
      this.$router.push('/reissuePassword');
    },
    pushRegistration : function () {
      this.$router.push('/registration');
    },
    signinTwitter : function () {
      const provider = new TwitterAuthProvider()
      const auth = getAuth();
      setPersistence(auth, browserLocalPersistence)
      .then(() => {
        return signInWithPopup(auth, provider)
        .then((result) => {
          console.log(result);
          let params = new URLSearchParams();
          params.append('twitterId', result.user.uid);
          params.append('twitterName', result.user.displayName);
          params.append('twitterIconUrl', result.user.providerData.photoURL);
          params.append('twitterCode', result.user.reloadUserInfo.screenName);
          let method = "t_login";

          this.login(params, method);
        }).catch((error) => {
          console.log(error.response);
        });
      }).catch((error) => {
        console.log(error.response);
      });
    },
    login : function (params, method) {
      axios.get('https://iphone-taro.sakura.ne.jp/yumedrop/sanctum/csrf-cookie').then(response => {
        console.log(response);
        axios.post(this.$root.SERVER_PATH + method, params)
        .then((response) => {
          let resData = response.data;
          if (resData["status"] == this.$root.API_FAILED_LOGIN) {
            //IDとPASSでログイン失敗
            this.errorMessageData.login = "ログインに失敗しました";
            return;
          }

          if (!this.$root.checkServerResponse(response)) {
            return;
          }

          this.$root.isLogin = true;
          this.$router.push('/mainmenu');
        })
        .catch((e) => {
          alert(e); 
        });
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.mailAddress = "";
      vm.password = "";
      vm.$root.setCurrentPage("LOGIN");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.mailAddress = "";
      vm.password = "";
      vm.$root.setCurrentPage("LOGIN");
    });
  }
}
</script>

