<template>
  <div class="edit container-fluid" style="background-color:#FFF5F6">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <span class="p14">作品を投稿</span>
          <div class="grayBar" />
          <div class="row">
            <div class="col-6">タイトル</div>
            <div class="col-6 constrains">(〇文字)</div>
          </div>
          <p class="errMsg" v-if="edErrorMessageData.title">▼ {{edErrorMessageData.title}}</p>
          <input type="text" class="input-text" v-model="edTitle" style="width:100%" maxlength="50">
          <div style="height:12px" />
          <div class="row">
            <div class="col-6">あらすじ</div>
            <div class="col-6 constrains">(〇文字)</div>
          </div>
          <p class="errMsg" v-if="edErrorMessageData.outline">▼ {{edErrorMessageData.outline}}</p>
          <textarea id="story" class="input-text" v-model="edOutline" name="story" rows="3" style="height:auto; width:100%" maxlength="150"></textarea>
          <div style="height:12px" />
          <div class="row">
            <div class="col-6">本文</div>
            <div class="col-6 constrains">(〇文字)</div>
          </div>
          <p class="errMsg" v-if="edErrorMessageData.body">▼ {{edErrorMessageData.body}}</p>
          <textarea id="story" class="input-text" v-model="edBody" name="story" rows="8" style="height:auto; width:100%" maxlength="30000"></textarea>
          <div style="height:12px" />
          変換項目<img class="cursor tips" v-on:click="this.$root.pushTips(0)" :src="this.$root.IMG_PATH + 'btn_hint.png'">
          <div style="height:8px" />
          <span class="btn btn-pink btn-icon btn-icon-plus" v-on:click="addConversion" style="width:145px">項目を追加</span>
          <div class="container-fluid">
            <div class="row" v-for="(rowData, index) in edConversionList" :key="index" style="margin-top:8px">
              <div class="col-7" style="padding:0 5px 0 0">
                <input type="text" class="input-text" maxlength="20" v-model="edConversionList[index][0]" style="width:100%" :placeholder="index == 0 ? '苗字を入力してください' : index == 1 ? '名前を入力してください' : '〇〇を入力してください'">
              </div>
              <div class="col-4" style="padding:0 5px 0 0">
                <input type="text" class="input-text" maxlength="20" v-model="edConversionList[index][1]" style="width:100%" :placeholder="index == 0 ? '苗字' : index == 1 ? '名前' : 'その他'">
              </div>
              <div class="col-1" style="padding:0;">
                <img :src="this.$root.IMG_PATH + 'btn_delete.png'" class="cursor" style="width:19px; height:19px" v-on:click="deleteConversion(index)">
              </div>
            </div>
          </div>
          <div class="grayBar" />
          シリーズ<img class="cursor tips" v-on:click="this.$root.pushTips(1)" :src="this.$root.IMG_PATH + 'btn_hint.png'">
          <select class="form-select" name="series" style="width:100%" v-model="edSeriesIndex">
            <option v-for="(seriesName, index) in edSeriesList" :key="index" v-bind:value="index">{{seriesName}}</option>
          </select>
          <div style="height:8px" />
          <input type="text" class="input-text" maxlength="30" v-model="edSeries" style="width:100%"><br>
          <div style="height:12px" />
          カテゴリー タグ
          <div class="row">
            <div class="col-4">
              <span :class="btnStyleCreation0" v-on:click="pushEdCreation(0)" style="width:95px">一次創作</span>
            </div>
            <div class="col-4">
              <span :class="btnStyleCreation1" v-on:click="pushEdCreation(1)" style="width:95px">二次創作</span>
            </div>
          </div>
          <div style="height:12px" />
          レーティング タグ
          <div class="row">
            <div class="col-4">
              <span :class="btnStyleRating0" v-on:click="pushEdRating(0)" style="width:95px">全年齢</span>
            </div>
            <div class="col-4">
              <span :class="btnStyleRating1" v-on:click="pushEdRating(1)" style="width:95px">R-18</span>
            </div>
            <div class="col-4">
              <span :class="btnStyleRating2" v-on:click="pushEdRating(2)" style="width:95px">R-18 G</span>
            </div>
          </div>
          <div style="height:12px" />
          <div v-show="this.edRatingIndex == '0'">
            フィルター
            <select  class="form-select" name="filter" style="width:100%" v-model="edFilter">
              <option value="0">通常</option>
              <option value="1">ワンクッション</option>
              <option value="2">ネタバレ</option>
              <!-- <option value="4">パスワード</option> -->
            </select>
          </div>
          <div style="height:12px" />
          <div class="row">
            <div class="col-6">ジャンル タグ</div>
            <div class="col-6 constrains">(〇)</div>
          </div>
          <input type="text" class="input-text" maxlength="50" v-model="edTags" style="width:100%" placeholder="例) aaa,bbb">
          <div class="grayBar" />
          公開方法
          <select class="form-select" name="publishing" style="width:100%" v-model="edPublishing">
            <option value="0">全体公開</option>
            <!-- <option value="5">Twitter相互フォロワー限定</option>
            <option value="6">Twitterフォロワー限定</option>
            <option value="7">Twitterリスト限定</option>
            <option value="8">Twitterリツイート限定</option> -->
            <option value="99">非公開</option>
          </select>
          <div style="height:12px" />
          検索一覧に載せる<br>
          <span style="color:#B3B3B3">全体公開/ワンクッション/R18/R18G以外は検索一覧に載りません</span><br>
          <div class="switchArea">
            <input type="checkbox" id="switchSe" v-model="edSearchable">
            <label for="switchSe"><span></span></label>
            <div id="swImg"></div>
          </div>
          <!-- <div class="search" id="makeImg">
            <input type="radio" v-model="edSearchable" value="1">
            <label for="sample1on">YES</label>
            <input type="radio" v-model="edSearchable" value="0">
            <label for="sample1off">NO</label>
          </div> -->
          <div class="grayBar" />
          <span class="errMsg" v-if="edErrorMessageData.api"><br>{{edErrorMessageData.api}}</span>
          <div v-if="this.edIsNew">
            <span class="btn btn-pink" v-on:click="PushBtn('NEW')" style="width:160px">作品を投稿</span>
          </div>
          <div v-if="!this.edIsNew">
            <span class="btn btn-pink" v-on:click="PushBtn('UPDATE')" style="width:160px">作品を更新</span>
            <br>
            <span class="btn btn-gray" v-on:click="pushDelete" style="width:160px">作品を削除</span>
          </div>
        </div>
      </div>
      <ad-area></ad-area>
    </div>
  </div>
  <popup-base ref="popup" @popupAction="popupAction" :kbn="this.popupKbn" :params="this.popupParams"></popup-base>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Edit',
  components: {
  },
  data() {
    return {
      edPostId : "",
      edTitle : "",
      edOutline : "",
      edBody : "",
      edConversionList : [["",""], ["",""], ["",""]],
      edSeries : "",
      edSeriesIndex : 0,
      edRatingIndex : 0,
      edCreationIndex : 0,
      edTags : "",
      edFilter : 0,
      edPublishing : 0,
      edSearchable : true,

      edIsNew : false,
      edSeriesList : [],

      edErrorMessageData : {
        title : "",
        outline : "",
        body : "",
        conversion : "",
        series : "",
        tags : "",
        api : "",
      },

      popupKbn : "",
      popupParams : {
        title : "",
        body : "",
        tweet : "",
      },
    };
  },
  computed : {
    btnStyleRating0 : function() {
      if (this.edRatingIndex == 0) { return "btn btn-sq btn-pink"; } else {  return "btn btn-sq btn-sq-gray"; }
    },
    btnStyleRating1 : function() {
      if (this.edRatingIndex == 1) { return "btn btn-sq btn-pink"; } else {  return "btn btn-sq btn-sq-gray"; }
    },
    btnStyleRating2 : function() {
      if (this.edRatingIndex == 2) { return "btn btn-sq btn-pink"; } else {  return "btn btn-sq btn-sq-gray"; }
    },
    btnStyleCreation0 : function() {
      if (this.edCreationIndex == 0) { return "btn btn-sq btn-pink"; } else {  return "btn btn-sq btn-sq-gray"; }
    },
    btnStyleCreation1 : function() {
      if (this.edCreationIndex == 1) { return "btn btn-sq btn-pink"; } else {  return "btn btn-sq btn-sq-gray"; }
    }
  },
  methods : {
    deleteConversion : function(index) {
      this.edConversionList.splice(index, 1);
    },
    addConversion : function () {
      this.edConversionList.push(["", ""]);
    },
    pushEdRating : function (val) {
      console.log(val);
      this.edRatingIndex = val;
    },
    pushEdCreation : function (val) {
      this.edCreationIndex = val;
    },
    PushBtn : function(name) {
      if (name == "NEW" || name == "UPDATE") {
        //入力チェック
        this.edErrorMessageData.title = "";
        this.edErrorMessageData.outline = "";
        this.edErrorMessageData.body = "";
        this.edErrorMessageData.conversion = "";
        this.edErrorMessageData.series = "";
        this.edErrorMessageData.tags = "";
        this.edErrorMessageData.api = "";

        //トリム
        this.edTitle = this.edTitle.trim();
        this.edOutline = this.edOutline.trim();
        this.edBody = this.edBody.trim();
        
        
        //入力チェック
        if (this.edTitle == "") {
          this.edErrorMessageData.title = "タイトルが未入力です";
        } else if (this.edTitle.length > 50) {
          this.edErrorMessageData.title = "タイトルが50文字を超えています";
        }
        
        if (this.edOutline == "") {
          this.edErrorMessageData.outline = "あらすじが未入力です";
        } else if (this.edOutline.length > 150) {
          this.edErrorMessageData.outline = "あらすじが150文字を超えています";
        }
        
        if (this.edBody == "") {
          this.edErrorMessageData.body = "本文が未入力です";
        } else if (this.edBody.length > 30000) {
          this.edErrorMessageData.body = "本文が30000文字を超えています";
        }

        if (this.edErrorMessageData.title != "" || this.edErrorMessageData.outline != "" || this.edErrorMessageData.body != "") {
          return;
        }
        
        //変換項目成型
        var conversionStr = "";
        for (let i = 0; i < this.edConversionList.length; i++) {
          const element = this.edConversionList[i];
          let val1 = element[0].trim();
          let val2 = element[1].trim();
          if (val1 != "" && val2 != "") {
            conversionStr += val1 + ":" + val2 + ",";
          } else if (val1 != "" && val2 == "") {
            //片方だけ空文字
            this.edErrorMessageData.conversion = "左側に未入力欄があります";
            return;
          } else if (val1 == "" && val2 != "") {
            //片方だけ空文字
            this.edErrorMessageData.conversion = "右側に未入力欄があります";
            return;
          } else if (val1.length > 20) {
            this.edErrorMessageData.conversion = "左側が20文字を超えています";
            return;
          } else if (val2.length > 20) {
            this.edErrorMessageData.conversion = "右側が20文字を超えています";
            return;
          }
        }

        //最後のカンマを除去
        if (conversionStr != "") {
          conversionStr = conversionStr.substr(0, conversionStr.length - 1);
        }

        //シリーズ選択
        //トリム
        this.edSeries = this.edSeries.trim();
        
        var seriesStr = this.edSeries;
        if (seriesStr == "") {
          seriesStr = this.edSeriesList[this.edSeriesIndex];
        }

        //入力チェック
        if (seriesStr.length > 30) {
          this.edErrorMessageData.series = "シリーズが30文字を超えています";
          return;
        }

        //ジャンルタグ
        this.edTags = this.edTags.trim();
        if (this.edTags.length > 50) {
          this.setErrorMessageData.tags = "ジャンルタグが50文字を超えています";
          return;
        }

        //#がついていれば除去
        this.edTags = this.edTags.replace("#", "");
        this.edTags = this.$root.arrangeStr(this.edTags, ',');

        //異常値チェック
        if ((this.edRatingIndex != 0 && this.edRatingIndex != 1 && this.edRatingIndex != 2) ||
         (this.edCreationIndex != 0 && this.edCreationIndex != 1) || 
         (this.edFilter != 0 && this.edFilter != 1 && this.edFilter != 2 && this.edFilter != 3) || 
         (this.edPublishing != 0 && this.edPublishing != 99) || 
         (this.edSearchable != false && this.edSearchable != true)) {
          //エラー
          this.$router.push({path : "/error", query : {errorCode : this.$root.API_FAILED_PARAM, message :""}});
          return;
        }
   
        let params = new URLSearchParams();
        let method = "";
        let pBody = "";
        if (name == "NEW") {
          method = "insertPost";
          params.append('postId', "");
          pBody = "作品の投稿が完了しました";
        } else if (name == "UPDATE") {
          method = "updatePost";
          params.append('postId', this.edPostId);
          pBody = "作品の更新が完了しました";
        }
        
        params.append('title', this.edTitle);
        params.append('outline', this.edOutline);
        params.append('body', this.edBody);
        params.append('conversion', conversionStr);
        params.append('series', seriesStr);
        params.append('rating', this.edRatingIndex);
        params.append('creation', this.edCreationIndex);
        params.append('tags', this.edTags);
        params.append('filter', this.edFilter);
        params.append('publishing', this.edPublishing);
        params.append('searchable', this.edSearchable);

        axios.post(this.$root.SERVER_PATH + method, params)
        .then((response) => {
          let resData = response.data;
          if (resData["status"] == this.$root.API_FAILED_NODATA) {
            //データなし
            this.$router.replace({path : "/error", query : {errorCode : resData["status"], message : "選択した投稿は存在しません"}});
            return;
          }
          
          if (!this.$root.checkServerResponse(response)) {
            return;
          }

          let tweetBase = "https://twitter.com/intent/tweet?button_hashtag=LoveTwitter&ref_src=twsrc%5Etfw";
          let tweetBody = "&url=https://www.google.co.jp/&text=" + this.edTitle + "&hashtags=yumedrop";

          this.popupKbn = "POST";
          this.popupParams = {
            title : "",
            body : pBody,
            tweet : tweetBase + tweetBody,
          }
          this.$refs.popup.showModal();
          


        }).catch((error) => {
            console.log(error.response);
        });
      }
    },
    popupAction: function(val) {
      if (val == "DELETE") {
        // 投稿削除
        console.log(val);
        let params = new URLSearchParams();
        params.append('postId', this.edPostId);
        axios.post(this.$root.SERVER_PATH + 'deletePost', params)
        .then((response) => {
          if (!this.$root.checkServerResponse(response)) {
            return;
          }
          this.popupKbn = "DELETE_COMP";
          this.popupParams = {
            title : "削除しました",
            body : "",
          }
          this.$refs.popup.showModal();
        });
      } else if (val == "DELETE_COMP" || val == "POST_COMP") {
        this.$router.push("/postlist");
      }

    },
    getMySeriesList : function () {
      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + 'getMySeriesList', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response)) {
          return;
        }
        let resData = response.data;

        let resDataList = resData["seriesList"];
        this.edSeriesList = [];
        for (let i = 0; i < resDataList.length; i++) {
          this.edSeriesList.push(resDataList[i]);
        }
        
        for (let i = 0; i < this.edSeriesList.length; i++) {
          if (this.edSeriesList[i] == this.edSeries) {
            this.edSeriesIndex = i;
            this.edSeries = "";
            break;
          }
        }
      }).catch((error) => {
        console.log(error.response);
      });
    },
    pushDelete : function () {
      this.popupKbn = "DELETE";
      this.popupParams = {
        title : "本当に削除しますか？",
        body : "一度削除されたものを\n元に戻すことはできません",
      }
      this.$refs.popup.showModal();
    },
    getPostData : function () {
      let postId = this.$route.params.postId;

      let params = new URLSearchParams();
      params.append('postId', postId);
      axios.post(this.$root.SERVER_PATH + 'getMyPostData', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          //データなし
          this.$router.replace({path : "/error", query : {errorCode : resData["status"], message : "選択した投稿は存在しません"}});
          return;
        }

        if (!this.$root.checkServerResponse(response)) {
          return;
        }
        let rowData = resData["postData"];

        this.edIsNew = false;
        this.edPostId = rowData["post_id"];
        this.edTitle = rowData["title"];
        this.edOutline = rowData["outline"];
        this.edBody = rowData["body"];
        this.edConversionList = rowData.conversionList;
        this.edSeries = rowData["series"];
        this.edRatingIndex = rowData["rating"];
        this.edCreationIndex = rowData["creation"];
        this.edTags = rowData["tags"];
        this.edFilter = rowData["filter"];
        this.edPublishing = rowData["publishing"];
        this.edSearchable = rowData["searchable"];

        var conversionStr = rowData["conversion"];
        if (conversionStr == "") {
          this.edConversionList = [["",""], ["",""], ["",""]];
        } else {
          this.edConversionList = [];
          var cArray = conversionStr.split(',');
          for (let i = 0; i < cArray.length; i++) {
            const element = cArray[i];
            var ccArray = element.split(':');
            this.edConversionList.push([ccArray[0], ccArray[1]]);
          }
        }

        //シリーズ一覧編集
        let resDataList = resData["seriesList"];
        this.edSeriesList = [];
        for (let i = 0; i < resDataList.length; i++) {
          this.edSeriesList.push(resDataList[i]);
        }

        for (let i = 0; i < this.edSeriesList.length; i++) {
          if (this.edSeriesList[i] == this.edSeries) {
            this.edSeriesIndex = i;
            this.edSeries = "";
            break;
          }
        }
      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      var postId = vm.$route.params.postId;
      
      if (postId == "new") {
        //新規投稿
        vm.edPostId = "";
        vm.edIsNew = true;
        vm.edTitle = "";
        vm.edOutline = "";
        vm.edBody = "";
        vm.edConversionList = [["",""], ["",""], ["",""]];
        vm.edSeries = "";
        vm.edRatingIndex = 0;
        vm.edCreationIndex = 0;
        vm.edTags = "";
        vm.edFilter = 0;
        vm.edPublishing = 0;
        vm.edSearchable = true;
        
        vm.edSeriesIndex = 0;
  
        //シリーズ一覧取得
        vm.getMySeriesList();

      } else {
        //編集
        vm.getPostData();
      }
      vm.$root.setCurrentPage("EDIT");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      var postId = vm.$route.params.postId;

      if (postId == "new") {
        //新規投稿
        vm.edPostId = "";
        vm.edIsNew = true;
        vm.edTitle = "";
        vm.edOutline = "";
        vm.edBody = "";
        vm.edConversionList = [["",""], ["",""], ["",""]];
        vm.edSeries = "";
        vm.edRatingIndex = 0;
        vm.edCreationIndex = 0;
        vm.edTags = "";
        vm.edFilter = 0;
        vm.edPublishing = 0;
        vm.edSearchable = true;
        
        vm.edSeriesIndex = 0;
  
        //シリーズ一覧取得
        vm.getMySeriesList();

      } else {
        //編集
        vm.getPostData();
      }
      vm.$root.setCurrentPage("EDIT");
    });
  }
}
</script>

