import { createRouter, createWebHashHistory } from 'vue-router'
import Top from '../views/Top.vue'
import Login from '../views/Login.vue'
import Registration from '../views/Registration.vue'
import MainMenu from '../views/MainMenu.vue'
import Search from '../views/Search.vue'
import Follow from '../views/Follow.vue'
import Setting from '../views/Setting.vue'
import Edit from '../views/Edit.vue'
import EditResult from '../views/EditResult.vue'
import PostList from '../views/PostList.vue'
import UserPage from '../views/UserPage.vue'
import Reading from '../views/Reading.vue'
import Bookmark from '../views/Bookmark.vue'
import Error from '../views/Error.vue'
import RegistrationConfirmation from '../views/RegistrationConfirmation.vue'
import ReissuePassword from '../views/ReissuePassword.vue'
import ResetPassword from '../views/ResetPassword.vue'
import Test from '../views/Test.vue'

const routes = [
  {
    path: '/',
    name: 'Top',
    component: Top
  },
  {
    path: '/mainmenu',
    name: 'MainMenu',
    component: MainMenu
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/registration',
    name: 'Registration',
    component: Registration
  },
  {
    path: '/registrationConfirmation/:tempId',
    name: 'RegistrationConfirmation',
    component: RegistrationConfirmation
  },
  {
    path: '/reissuePassword',
    name: 'ReissuePassword',
    component: ReissuePassword
  },
  {
    path: '/resetpassword/:tempId',
    name: 'ResetPassword',
    component: ResetPassword
  },
  {
    path: '/error',
    name: 'Error',
    component: Error
  },
  {
    path: '/search',
    name: 'Search',
    component: Search
  },
  {
    path: '/follow',
    name: 'Follow',
    component: Follow
  },
  {
    path: '/setting',
    name: 'Setting',
    component: Setting
  },
  {
    path: '/edit/:postId',
    name: 'Edit',
    component: Edit
  },
  {
    path: '/editresult',
    name: 'EditResult',
    props: true,
    component: EditResult
  },
  {
    path: '/postlist',
    name: 'PostList',
    component: PostList
  },
  {
    path: '/bookmark',
    name: 'Bookmark',
    component: Bookmark
  },
  {
    path: '/userpage/:userId',
    name: 'UserPage',
    component: UserPage
  },
  {
    path: '/reading/:postId',
    name: 'Reading',
    component: Reading
  },
  {
    path: '/test',
    name: 'Test',
    component: Test
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
